import { z } from 'zod';
import { TemplateInterface } from './template.interface';
import {
  TemplateImplementationOverridesInterface,
  TemplateImplementationOverridesSchema,
} from './template-implementation-overrides.interface';

export const TemplateImplementationSchema = z.object({
  templateVersion: z.literal('latest').or(z.string().min(1)),
  name: z.string().min(1),
  description: z.string().min(1),
  overrides: TemplateImplementationOverridesSchema,
});

export const TemplateImplementationCreateSchema =
  TemplateImplementationSchema.extend({
    templateId: z.string().min(1),
    tenantId: z.string().min(1),
  });

export interface TemplateImplementationInterface {
  id: string;
  templateId: string;
  template: TemplateInterface;
  templateVersion: string | 'latest';
  tenantId: string;
  name: string;
  description: string;
  overrides: TemplateImplementationOverridesInterface;
}
